<template>
	<view class="nav">
		<view class="content">
			<view class="nav-top">
				<view class="nav-top-left">
					<view class="text">{{nowTime}}</view>
					<view class="name">{{nickname}}</view>
				</view>
				<view class="nav-top-right">
					<text class="iconfont icon-sousuo"></text>
					<input class="inp" placeholder="请输入商品名称" v-model="sousuoname" confirm-type="search"
						@confirm="sousuo">
				</view>
			</view>
			<view class="nav-banner">
				<swiper class="nav-img" autoplay="true" :indicator-dots="true" :interval="3000" :duration="1000"
					indicator-color="rgba(255, 255, 255, 3)">
					<swiper-item class="nav-item-img" v-for="(item,index) in listbanner" :key="index">
						<image class="banner-img" :src="item.bannerImg"></image>
					</swiper-item>
				</swiper>
			</view>
			<view class="nav-hot">
				<view class="nav-hot-title">
					热卖推荐
				</view>
				<view class="nav-hot-content">
					<view class="nav-content" v-for="(item,index) in listhot" :key='index'>
						<view class="nav-content-img">
							<view class="hot">hot</view>
							<image class="img" :src="item.largeImg"></image>
						</view>
						<view class="name">{{item.name}}</view>
						<view class="enname">{{item.enname}}</view>
						<view class="price">￥{{item.price}}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import iconfont from '../../static/style/iconfont.css'
	import request from '../request/request.js'
	export default {
		data() {
			return {
				listbanner: '',
				listhot: '',
				nowTime: '',
				sousuoname: '',
				nickname:''
			}
		},
		methods: {
			async getRequest() {
				let res = await uni.request({
					url: "http://www.kangliuyong.com:10002/banner",
					data: {
						appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA='
					}
				})
				let reshot = await uni.request({
					url: 'http://www.kangliuyong.com:10002/typeProducts',
					data: {
						appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
						key: 'isHot',
						value: 1
					}
				})
				this.listhot = reshot[1].data.result
				this.listbanner = res[1].data.result
				request({
					url: '/findMy',
					method: "GET",
					data: {},
					success: (res) => {
						this.nickname = res.data.result[0].nickName
						console.log(this.nickname)
					}
				})
			},
			getTime() {
				var time = (new Date()).getHours();
				console.log(time)
				if (time > 12) {
					this.nowTime = '下午好'
				}

			},
			sousuo() {
				uni.navigateTo({
					url: `../sousuo/sousuo?${this.sousuoname}`
				})
			}

		},
		onLoad() {
			this.getRequest()
			this.getTime()
		}
	}
</script>

<style lang="less" scoped>
	.nav {
		background-color: #f5f5f5;

		.content {
			margin: 20rpx;
			background-color: #FFFFFF;

			.nav-top {
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin: 30rpx;

				.nav-top-left {

					display: flex;

					.text {
						font-size: 25rpx;
						font-weight: 800;
						color: #646566;
					}

					.name {
						margin-left: 30rpx;
						font-size: 25rpx;
						font-weight: 800;
						color: #0c34ba;
					}
				}

				.nav-top-right {
					position: relative;

					.icon-sousuo {
						position: absolute;
						color: #0c34ba;
						left: 30rpx;
						top: 18rpx;
					}

					.inp {
						width: 400rpx;
						height: 60rpx;
						text-align: center;
						border-radius: 30rpx;
						background-color: #f7f8fa;
						font-size: 20rpx;
					}
				}
			}

			.nav-banner {
				margin: 30rpx;

				.nav-img {
					height: 450rpx;
				}

				.banner-img {
					width: 100%;
					height: 450rpx;
					border-radius: 20rpx;
				}

				::v-deep .uni-swiper-dot {
					width: 50rpx;
					height: 10rpx;
					border-radius: 10rpx;
				}
			}

			.nav-hot {
				margin: 30rpx;

				.nav-hot-title {
					width: 200rpx;
					height: 80rpx;
					color: #FFFFFF;
					font-size: 35rpx;
					font-weight: 800;
					background-color: #0c34ba;
					text-align: center;
					line-height: 80rpx;
					border-top-right-radius: 40rpx;
				}

				.nav-hot-content {
					display: flex;
					flex-wrap: wrap;

					.nav-content {
						margin: 34rpx;
						width: 250rpx;
						position: relative;

						.nav-content-img {
							.hot {
								position: absolute;
								left: 30rpx;
								width: 70rpx;
								height: 70rpx;
								line-height: 70rpx;
								text-align: center;
								color: #FFFFFF;
								background-color: #0c34ba;
								border-bottom-left-radius: 20rpx;
								border-bottom-right-radius: 20rpx;
								z-index: 1;
							}

							.img {

								width: 300rpx;
								height: 300rpx;
								border-radius: 30rpx;
							}
						}

						.name {
							color: #646566;
						}

						.enname {
							font-size: 20rpx;
							color: #999999;
						}

						.price {
							font-weight: 800;
							margin-top: 20rpx;
							color: #0c34ba;
						}
					}
				}
			}
		}
	}
</style>
